import * as React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';

import { alpha, styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import Tooltip from '@mui/material/Tooltip';
import TableCell from '@mui/material/TableCell';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableSortLabel from '@mui/material/TableSortLabel';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import FilterListIcon from '@mui/icons-material/FilterList';
import FilterListOffIcon from '@mui/icons-material/FilterListOff';
import { visuallyHidden } from '@mui/utils';

import Loader from '../components/Loader';

import { TrucksQuery, TruckDelete } from '../api';
import { useSnackbar } from '../hooks';
import { Order, Truck } from '../utils/dataTypes';

interface HeadCell {
  id: string;
  disablePadding: boolean;
  orderField?: keyof Truck;
  getData: (row: Truck) => { data: JSX.Element | string; title?: string };
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: 'truckNumber',
    orderField: 'truckNumber',
    getData: (row) => ({ data: `${row.truckNumber}` }),
    numeric: true,
    disablePadding: false,
    label: 'Truck Number',
  },
  {
    id: 'mainContactInfo',
    getData: (row) => {
      const coordinator = row.coordinator;
      const owner = row.owner;
      let path = '';
      let type = '';
      let firstRow = '';
      let secondRow = '';
      if (coordinator) {
        path = `./coordinators/view/${coordinator.id}`;
        type = '(C) ';
        firstRow = `${coordinator.fullName}`;
        secondRow = `${coordinator?.phone}`;
      } else if (owner) {
        path = `./owners/view/${owner.id}`;
        type = `${owner.type === 'Owner' ? '(O) ' : ''}${
          owner.type === 'OwnerDriver' ? '(O/d) ' : ''
        }`;
        firstRow = `${owner.fullName}`;
        secondRow = `${owner?.phone}`;
      }
      return {
        data: (
          <span>
            <b>{type}</b>
            <a href={path} target="_blank" onClick={stopPropagation}>
              {firstRow}
            </a>
            <br />
            <a
              href={secondRow ? `tel:${secondRow}` : undefined}
              target="_blank"
              onClick={stopPropagation}
            >
              {secondRow || ' '}
            </a>
          </span>
        ),
        title: type + '' + firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Main Contact info',
  },
  {
    id: 'status',
    orderField: 'status',
    getData: (row) => ({ data: `${row.status}` }),
    numeric: false,
    disablePadding: true,
    label: 'Status',
  },
  {
    id: 'location',
    getData: (row) => {
      if (row?.lastCity) {
        const path = `./locations/view/${row.lastCity.id}`;
        const firstRow = `${row.lastCity.name}, ${row.lastCity.stateCode}, ${row.lastCity.zipCode},`;
        const secondRow = `${
          row.locationUpdatedAt
            ? new Date(row.locationUpdatedAt).toLocaleString()
            : ''
        }`;

        return {
          data: (
            <span>
              <a href={path} target="_blank" onClick={stopPropagation}>
                {firstRow}
              </a>
              <br />
              {secondRow}
            </span>
          ),
          title: firstRow + '\n' + secondRow,
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'Last known location',
  },
  {
    id: 'truckInfo',
    getData: (row) => {
      const firstRow = row.type ? `${row.type}` : '';
      const secondRow = row.equipment ? `${row.equipment.join(', ')}` : '';

      return {
        data: (
          <span>
            {firstRow}
            <br />
            {secondRow}
          </span>
        ),
        title: firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Truck info',
  },
  {
    id: 'dimsPayload',
    getData: (row) => {
      const firstRow = row.doorDims ? `${row.doorDims}` : '';
      const secondRow = row.payload ? `${row.payload}` : '';

      return {
        data: (
          <span>
            {firstRow}
            <br />
            {secondRow}
          </span>
        ),
        title: firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'DIMS/Payload',
  },
  {
    id: 'driverInfo',
    getData: (row) => {
      const driver = row.driver;
      let path = '';
      let type = '';
      let firstRow = '';
      let secondRow = ''; // OwnerDriver  CoordinatorDriver    Driver
      if (driver) {
        path = `./drivers/view/${driver.id}`;
        firstRow = `${driver.fullName}`;
        secondRow = `${driver.phone}`;
        if (driver.type === 'OwnerDriver') {
          type = '(O/d) ';
        }
        if (driver.type === 'CoordinatorDriver') {
          type = '(C/d) ';
        }
        if (driver.type === 'Driver') {
          type = '(D) ';
        }
      }
      return {
        data: (
          <span>
            <b>{type}</b>
            <a
              href={firstRow ? path : undefined}
              target="_blank"
              onClick={stopPropagation}
            >
              {firstRow}
            </a>
            <br />
            <a
              href={secondRow ? `tel:${secondRow}` : undefined}
              target="_blank"
              onClick={stopPropagation}
            >
              {secondRow || ' '}
            </a>
          </span>
        ),
        title: type + '' + firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Driver info',
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order | undefined;
  orderBy: string | undefined;
  rowCount: number;
}

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.background.default,
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  maxWidth: '100px',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  overflow: 'hidden',
}));

function stopPropagation(e: React.SyntheticEvent) {
  e.stopPropagation();
}

function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof Truck) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <StyledTableHead>
      <TableRow>
        <StyledTableCell padding="checkbox">
          <Checkbox
            name={'enhanced-table-checkbox-select-all'}
            color="primary"
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{
              'aria-label': 'enhanced-table-checkbox-select-all',
            }}
          />
        </StyledTableCell>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'center'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={headCell.orderField && orderBy === headCell.orderField}
              hideSortIcon={!headCell.orderField}
              direction={orderBy === headCell.orderField ? order : 'asc'}
              onClick={
                headCell.orderField
                  ? createSortHandler(headCell.orderField)
                  : () => {
                      return;
                    }
              }
            >
              {headCell.label}
              {headCell.orderField && orderBy === headCell.orderField ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </StyledTableCell>
        ))}
      </TableRow>
    </StyledTableHead>
  );
}

interface EnhancedTableToolbarProps {
  selected: readonly string[];
  setQueryParams: React.Dispatch<React.SetStateAction<Record<string, number>>>;
}

function EnhancedTableToolbar(props: EnhancedTableToolbarProps) {
  const { selected, setQueryParams } = props;
  const [truckNumberSearch, setTruckNumberSearch] = React.useState<number>(0);
  const [truckNumberChanged, setTruckNumberChanged] =
    React.useState<boolean>(false);
  const location = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();

  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(TruckDelete, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Deleted',
      });
      queryClient.invalidateQueries(['trucks']);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const truckNumberHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (Number.isFinite(Number(event.target.value))) {
      setTruckNumberSearch(Number(event.target.value));
      setTruckNumberChanged(true);
    }
  };
  const truckClearHandler = () => {
    setTruckNumberChanged(false);
    setTruckNumberSearch(0);
    setQueryParams({});
  };
  const filterHandler = () => {
    setTruckNumberChanged(false);
    setQueryParams({ truckNumber: truckNumberSearch });
  };
  const createHandler = () => {
    navigate('./create', {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const editHandler = () => {
    navigate(`./edit/${selected[0]}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const deleteHandler = () => {
    mutate(`${selected[0]}`);
  };

  return (
    <Toolbar
      sx={{
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        ...(selected.length > 0 && {
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.activatedOpacity,
            ),
        }),
      }}
    >
      {selected.length > 0 ? (
        <Typography
          sx={{ flex: '1 1 100%' }}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {selected.length} selected
        </Typography>
      ) : (
        <Typography
          sx={{
            flex: '1 1 100%',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden',
          }}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          Trucks
        </Typography>
      )}
      <TextField
        label="Truck Number"
        value={truckNumberSearch || ''}
        onChange={truckNumberHandler}
        size="small"
      />
      <Tooltip title="Filter list">
        {(truckNumberChanged || !truckNumberSearch) &&
        !(truckNumberChanged && !truckNumberSearch) ? (
          <span>
            <IconButton disabled={!truckNumberSearch} onClick={filterHandler}>
              <FilterListIcon />
            </IconButton>
          </span>
        ) : (
          <IconButton onClick={truckClearHandler}>
            <FilterListOffIcon />
          </IconButton>
        )}
      </Tooltip>
      <Box sx={{ p: 1, display: 'inline-flex' }}>
        <Tooltip title="Create">
          <span>
            <IconButton onClick={createHandler}>
              <AddIcon />
            </IconButton>
          </span>
        </Tooltip>
        <Tooltip title="Edit">
          <span>
            <IconButton disabled={selected.length !== 1} onClick={editHandler}>
              <EditIcon />
            </IconButton>
          </span>
        </Tooltip>
        <Tooltip title="Delete">
          <span>
            <IconButton
              disabled={selected.length !== 1}
              onClick={deleteHandler}
            >
              <DeleteIcon />
            </IconButton>
          </span>
        </Tooltip>
      </Box>
      <Loader visible={isLoading} />
    </Toolbar>
  );
}

export default function EnhancedTable() {
  const [order, setOrder] = React.useState<Order | undefined>('asc');
  const [orderBy, setOrderBy] = React.useState<keyof Truck | undefined>(
    'truckNumber',
  );
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = React.useState<number>(15);
  const [queryParams, setQueryParams] = React.useState<Record<string, any>>({});
  const tableContainerRef = React.useRef(null);
  const location = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading,
    isFetching,
    data: { items, total } = { items: [], total: 0 },
  } = useQuery({
    queryKey: ['trucks', page, rowsPerPage, orderBy, order, queryParams],
    queryFn: TrucksQuery,
    keepPreviousData: true,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });
  const rows: Truck[] = items || [];

  React.useEffect(() => {
    if (tableContainerRef.current) {
      const container = tableContainerRef.current as HTMLElement;
      container.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    }
  }, [rows]);

  React.useEffect(() => {
    const newSelected: string[] = [];
    selected.forEach((selectedItem) => {
      const selectedRow = rows.find((row) => row.id === selectedItem);
      if (selectedRow) {
        newSelected.push(selectedItem);
      }
    });
    setSelected(newSelected);
  }, [rows]);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    const isDesc = orderBy === property && order === 'desc';
    if (isDesc) {
      setOrder(undefined);
      setOrderBy(undefined);
    } else {
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(property);
    }
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n: Truck) => n.id);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event: React.MouseEvent<unknown>, name: string) => {
    navigate(`./view/${name}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };

  const handleSelect = (event: React.MouseEvent<unknown>, name: string) => {
    event.stopPropagation();
    const selectedIndex = selected.indexOf(name);
    let newSelected: readonly string[] = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  return (
    <Box sx={{ width: '100%', height: '100%' }}>
      <Paper sx={{ width: '100%', height: '100%', backgroundColor: 'unset' }}>
        <EnhancedTableToolbar
          selected={selected}
          setQueryParams={(query) => {
            setQueryParams(query);
            setPage(0);
          }}
        />
        <TableContainer
          ref={tableContainerRef}
          sx={{ height: 'calc(100% - 115px)' }}
        >
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size="small"
            stickyHeader
          >
            <EnhancedTableHead
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {rows.map((row, index) => {
                const isItemSelected = isSelected(`${row.id}`);
                const labelId = `enhanced-table-checkbox-${index}`;
                return (
                  <TableRow
                    hover
                    onClick={(event) => handleClick(event, `${row.id}`)}
                    role="checkbox"
                    aria-checked={isItemSelected}
                    tabIndex={-1}
                    key={row.id}
                    selected={isItemSelected}
                    sx={{ cursor: 'pointer' }}
                  >
                    <StyledTableCell padding="checkbox">
                      <Checkbox
                        id={`${labelId}`}
                        color="primary"
                        checked={isItemSelected}
                        inputProps={{
                          'aria-labelledby': labelId,
                        }}
                        onClick={(event) => handleSelect(event, `${row.id}`)}
                      />
                    </StyledTableCell>
                    {headCells.map((cell) => {
                      const cellData = cell.getData(row);
                      return (
                        <StyledTableCell
                          key={cell.id}
                          title={cellData.title}
                          align={cell.numeric ? 'right' : 'center'}
                        >
                          {cellData.data}
                        </StyledTableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15, 20, 25, 35, 50]}
          component="div"
          count={total}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <Loader visible={isLoading || isFetching} />
    </Box>
  );
}
